.banner{
    height: calc(100vh);
    padding: 10px;
    margin-bottom: 5px;
    background: #222;
    display: flex;
    justify-content: center;
    align-items: center;
}

.name{
    text-align: right;
}

.middle-line{
    width: 5px;
    height: 100px;
    background: #fff;
    margin: 50px;
}

@media (max-width: 600px){
    .banner{
        height: calc(30vh - 25px);
    }

    .name{
        text-align: center;
    }

    .middle-line{
        display: none;
    }
}