.cardContainer{
    display: grid;
    height: 50vh;
    grid-template-columns: repeat(auto-fit, minmax(600px, 1fr));
    margin-left: 5px;
    margin-right: 5px;
    padding-left: 20px;
    padding-right: 20px;
}

.showcase-project-title{
    margin: 50px;
    text-align: center;
}

@media (max-width: 600px){
    .cardContainer{
        height: 70vh;
        grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    }
}