.cardArea{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    perspective: 1000px;
    -moz-perspective: 1000px;
    transition: all 0.5s ease;
}

.project-card{
    transform-style: preserve-3d;
    -moz-transform-style: preserve-3d;
    transition: all 0.5s ease-out;
    transform: rotateY(0deg);
    width: 100%;
    height: 100%;
    box-shadow: 0 20px 20px rgba(0, 0, 0, 0.2), 0px 0px 50px rgba(0, 0, 0, 0.2);
    background: #282828;
    padding: 20px;
    position: relative;
    display: block;
    border-radius: 30px;
}

.project-card.hide{
    transform-style: preserve-3d;
    -moz-transform-style: preserve-3d;
    transition: all 0.5s ease-out;
    transform: rotateY(90deg);
    width: 100%;
    height: 100%;
    box-shadow: 0 20px 20px rgba(0, 0, 0, 0.2), 0px 0px 50px rgba(0, 0, 0, 0.2);
    background: #282828;
    padding: 20px;
    position: relative;
    display: block;
    border-radius: 30px;
}

.project-card > .p-title{
    margin-bottom: 20px;
    transition: all 0.5s ease-out;
}

.project-card > .p-description{
    margin-bottom: 50px;
    transition: all 0.5s ease-out;
}

.project-card > .p-link{
    text-align: right;
    position: absolute;
    bottom: 0;
    right: 0;
    margin: 20px;
    transition: all 0.5s ease-out;
}

.project-card > .p-language{
    transition: all 0.5s ease-out;
    margin-bottom: 50px;
}